

  .header{
    background-image: linear-gradient(black, blue);
    color: blue;
    margin: 0 auto;
    padding: 0.1em 0;
    font-weight: bolder;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    width: 90%;
    border-radius: 5px;
    text-align: center;
    h2{
      text-shadow: 2px 2px 6px black,3px 3px 4px black,4px 4px 5px black;
    }
    
  }
  .section1{
    background-color: rgb(27, 3, 3);
    color: aliceblue;
    width: 50%;
    border-radius: 20px;
    margin: 3em auto;
    position: relative;
    padding: 1em;
    padding-bottom: 1em;
    .sec1{
      width: 90%;
      margin: 1em auto;
      text-align: center;
      font-size: 1.5em;
      font-family: Georgia, 'Times New Roman', Times, serif;
    }
    .pageChange{
      width: 100%;
      text-align: center;
      button{
        width: 60%;
        background-color: blue;
        color: #ffffff;
        border-radius: 10px;
        transition: 200ms ease-in;
        outline: none;
        &:hover{
          background-color: green;
        }
      }
    }
  }
  .section2{
    background-color: rgb(27, 3, 3);
    color: aliceblue;
    width: 50%;
    border-radius: 20px;
    margin: 3em auto;
    position: relative;
    padding: 1em;
    padding-bottom: 1em;
    
    .time{
      text-align: center;
      h2{
        text-transform: capitalize;
        text-shadow: 2px 2px 7px ,-2px -2px 10px blue;
      }
      span{
        font-size: 1.5em;
        font-weight: bolder;
        text-transform: capitalize;
      }
      input{
        margin: 0 0.5em;
        color: black;
      }
      .start{
        display: flex;
        justify-content: space-around;
        padding: 0.5em;
        border: 2px solid;
        margin: 0 1em;
      }
      .to{
        display: flex;
        justify-content: space-around;
        padding: 0.5em;
        border: 2px solid;
        margin: 1em;
      }
    }
    .descreption{
      padding: 1em;
      border: 2px solid;
      margin: 1em;
      input{
        width: 100%;
      }
    }
    .pageChange{
      margin: 1em auto;
      width: 90%;
      padding: 0em;
      display: flex;
      justify-content: space-evenly;
      button{
        background-color: blue;
        color: #ffffff;
        border-radius: 5px;
        padding: 0.5em;
        outline: none;
        transition: 200ms ease-in;
        &:hover{
          background-color:green;
        }
      }
    }
  }

  .section3{
    background-color: rgb(27, 3, 3);
    color: aliceblue;
    width: 50%;
    border-radius: 20px;
    margin: 3em auto;
    position: relative;

    padding: 0.5em;
    padding-bottom: 1em;
    .confirm{
      width: 70%;
      margin: 2em auto;
      padding: 1em;
      border-radius: 5px;
      box-shadow: 0 0 15px #ffffff,0 0 20px#aa7f7f;
      h2{
        text-transform: capitalize;
        font-weight: bolder;
        text-align: center;
      }
      span{
        color: blue;
      }
    }
    .pageChange{
      margin: 1em auto;
      width: 90%;
      padding: 0em;
      display: flex;
      justify-content: space-evenly;
      button{
        background-color: blue;
        color: #ffffff;
        border-radius: 5px;
        padding: 0.5em;
        outline: none;
        transition: 200ms ease-in;
        &:hover{
          background-color:green;
        }
      }
    }
  }

  .section4{
    background-color: rgb(27, 3, 3);
    color: aliceblue;
    width: 50%;
    border-radius: 20px;
    margin: 3em auto;
    position: relative;
    padding: 3em;
    padding-bottom: 3em;
    text-align: center;
    h2{
      color: green;
      font-weight: bolder;
      text-transform: uppercase;
      margin: 1em;
    }
    span{
      color: blue;
      background-color: honeydew;
      padding: 10px;
      border-radius: 3px;
      font-size: 1.3em;
      cursor: pointer;
      transition: 400ms ease-in;
      margin: 4em;
      &:hover{
        border: 3px solid ;
        background-color: blue;
        color: #ffffff;
      }
    }
  }

  @media (max-width: 700px){

    .section1{
    width: 100%;
    border-radius: 20px;
    margin: 0em auto;
    .sec1{
      width: 100%;
      font-size: 1.2em;
    }
  }
  .section2{
    width: 100%;
    padding: 0em;
    padding-bottom: 2em;
  }

  .section3{
    width: 100%;
    padding: 2em 0;
    .confirm{
      width: 90%;
      padding: 1em;
    }
  }

  .section4{
    background-color: rgb(27, 3, 3);
    color: aliceblue;
    width: 50%;
    border-radius: 20px;
    margin: 3em auto;
    position: relative;
    padding: 3em;
    padding-bottom: 3em;
    text-align: center;
    h2{
      color: green;
      font-weight: bolder;
      text-transform: uppercase;
      margin: 1em;
    }
    span{
      color: blue;
      background-color: honeydew;
      padding: 10px;
      border-radius: 3px;
      font-size: 1.3em;
      cursor: pointer;
      transition: 400ms ease-in;
      margin: 4em;
      &:hover{
        border: 3px solid ;
        background-color: blue;
        color: #ffffff;
      }
    }
  }


  }